body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  min-width: 100vh;
  overflow: scroll;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  min-height: 100vh;
  min-width: 100vw;
  text-align: center;
  background-color: rgba(219, 84, 97, 0.61);
  /*font-family: "American Typewriter";*/
  font-family: Verdana;
  font-weight: bold;
}

.AppNav {
  float: left;
}

.ViewGrid {
  max-width: 50vw;
  width: 50vw;
  min-height: 80vh;
  height: 80vh;
  background-color: #686963;
}

.Cart {
  max-width: 20vw;
  width: 20vw;
  min-height: 80vh;
  height: 80vh;
  background-color: #8AA29E;
}

.LandingLogo {
  width: 100vw;
  height: 70vh;
}

.LogoContainer {
  width: 100vw;
  height: 100vh;
  text-align: center;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.UserView {
    /*min-height: 100vh;*/
    /*min-width: 90vw;*/
    float: right;
    /*display: flex;*/
    background-color: rgba(60, 30, 8, 0.36);
}
.RestaurantCard {
    margin: 1vw;
    height: 50vh;
    width: 50vh;
    text-align: center;
    cursor: pointer;
    background-color: #3D5467;
}

.RestaurantCardContent {
    text-align: center;
    height: 45vh;
    width: 21.2vw;
    background-color: rgba(61, 84, 103, 0.65);
}

.RestaurantCardMedia {
    text-align: center;
    height: 30vh;
    width: 30vw;
    box-shadow: 3px 2px 2px 2px rgba(61, 84, 103, 0.65);
    border-radius: 20px;
}

.UserRestaurantPopover {
    height: 100vh;
    width: 100vw;
    float: right;
}

.UserRestaurantViewContainer {
    width: 100vw;
    text-align:center;
}

.UserRestaurantViewGrid {
    padding: 10vw;
    min-width: 20vw;
    width: 100vw;
}

.RestaurantMap {
    height: 50vh;
}

.RestaurantMapElement {
    height: 80vh;
}

.RestaurantMapContainerElement {
    height: 80vh;
}

.RestaurantMapLoader {
    height: 80vh;
}
.ActiveMenuPopover {
    width: 50vw;
    height: 70vh;
    margin: 4vw;
    background-color: #494a44;
}

.ActiveMenu {
    max-height: 600vh;
    min-height: 70vh;
    width: 50vw;
    background-color: #686963;
}

.ActiveMenu {
    padding: 2vw;
}

.ActiveMenuPopover {
    padding: 1vw;
}

.MenuItemCard {
    max-width: 30vw;
    max-height: 35vh;
    height: 20vh;
    width: 40vw;
    margin: 1vw;
}

.MenuItemInfo {
    font-size: 14pt;
    font-weight: bold;
}

.MenuItemCardContent {
    text-align:center;
}

.MenuBuyButton {
    float: right;
    margin-left: 4.5vw!important;
    border: 1px solid #494A44!important;
}

.CloseButton {
    cursor: pointer;
    margin-bottom: 2vh;
}

.UserMenu {
    vertical-align: top;
    /*width: 10vw;*/
    /*height: 100vh;*/
}

.MenuCardMedia {
    height: 15vh;
    width: 10vw;
    max-width: 10vw;
    max-height: 15vh;
    border: 2px solid #686963;
    border-radius: 50px;
    float: left;
}

.MenuHeader {
    margin-left: 2vw;
    text-align: center;
}

.UserMenuItem {
    height: 100%;
    width: 100%;
    vertical-align: top;
    background-color: #aeaeae;
}
.UserOrderViewContainer {
    width: 100vw;
    text-align:center;
    font-size: 18pt;
}

.UserOrderViewGrid {
    padding: 10vw;
    min-width: 20vw;
    width: 100vw;
    background-color: #8AA29E;
    border: 2px solid #0b445f;
}

.OrderCard, .PendingOrderCard, .FulfilledOrderCard {
    min-width: 30vw;
    min-height: 30vh;
    width: 30vw;
    height: 30vh;
    padding: 1vw;
    margin: 2vw;
    border-radius: 200%;
}

.OrderCard {
    background-color: #F1EDEE;
}

.PendingOrderCard {
    background-color: rgba(255, 97, 97, 0.77) !important;
}

.FulfilledOrderCard {
    background-color: rgba(93, 202, 101, 0.62) !important;
}

.OrderCardHeader {
    font-weight: bolder;
    font-size: 20pt;
}
.Cart {
    width: 92vw;
    max-width: 92vw;
    min-width: 200px;
    min-height: 20vh;
    margin: 4vw;
    background-color: #494a44;
}

.CartGrid {
    padding: 2vw;
}

.CartContent {
    width: 92vw;
    max-width: 92vw;
    min-width: 20vw;
    /*height: 80vh;*/
    max-height: 999vh;
    min-height: 70vh;
    text-align: center;
    background-color: #8AA29E;
    border: 2px solid #3f4845;
}

.CartItem {
    padding: 2vw;
    width: 12vw;
    max-width: 12vw;
    height: 20vh;
    max-height: 20vh;
    margin: 1vw;
    text-align: center;
}

.CartItemCardMedia {
    height: 15vh;
    width: 10vw;
    max-width: 10vw;
    max-height: 15vh;
    border: 1px solid #686963;
    border-radius: 100px;
}

.CartActions {
    margin-left: 70vw;
    max-width: 15vw;
    padding: 1vw;
    border-radius: 20px;
    bottom: 0;
    /*margin-top: 45vh;*/
    background-color: white;
}

.CheckoutButton {
    background-color: #686963;
    color: #686963;
    border: 1px solid black!important;
    margin-top: 1vh!important;
}
